.page {
  height: 100%;
}

.page__body {
  background-color: #ffffff;
  color: #444444;
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  min-height: 100%;
  margin: 0;

  @media (min-width: $desktop-width) {
    position: relative;
  }
}
